<svelte:options tag='my-cool-bar'></svelte:options> 
<script>
  import { scaleLinear } from "d3-scale";
  import { select } from "d3-selection";
  import { cubicOut } from "svelte/easing";
  import { transition } from "d3-transition";
  console.log("window", window.location.hostname);

  const reveal = (node, { delay, duration, infinite }) => {
    console.log("node", node);
    const yfinal = select(node).attr("y");
    const hfinal = select(node).attr("height");
    select(node).attr("class", "hovered");
    select(node).on("mouseover", function (d) {
      select(this).style("fill", "#D3D3D3");
    });
    select(node).on("mouseout", function (d) {
      select(this)
        .transition()
        .duration(600)
        .style("fill", function (d) {
          return `hsl(120,110%,50%)`;
        });
    });
    return {
      delay,
      duration,
      infinite,
      easing: cubicOut,
      tick: (t, u) => {
        select(node).attr("y", t * yfinal + yScale(0));
        select(node).attr("height", t * hfinal);
        setTimeout(function () {
          select(node).style("fill", `hsl(120,${u * 100 + 10}%,50%)`);
        }, 200);
        setTimeout(function () {
          select(node).style("fill", `hsl(120,${t * 100 + 10}%,50%)`);
        }, 1200);
      },
    };
  };

  const points = [
    { year: 1990, birthrate: 16.7 },
    { year: 1995, birthrate: 4.6 },
    { year: 2000, birthrate: 14.4 },
    { year: 2005, birthrate: 18 },
    { year: 2010, birthrate: 7 },
    { year: 2015, birthrate: 12.4 },
    { year: 2020, birthrate: 17 },
    { year: 2025, birthrate: 10.9 },
    { year: 2030, birthrate: 8 },
    { year: 2035, birthrate: 12.9 },
  ];

  const pointsx = [
    { year: 1990, birthrate: 6.7 },
    { year: 1995, birthrate: 4.6 },
    { year: 2000, birthrate: 14.4 },
    { year: 2005, birthrate: 18 },
    { year: 2010, birthrate: 7 },
    { year: 2015, birthrate: 12.4 },
    { year: 2020, birthrate: 17 },
    { year: 2025, birthrate: 10.9 },
    { year: 2030, birthrate: 8 },
    { year: 2035, birthrate: 12.9 },
  ];
  const xTicks = [1990, 1995, 2000, 2005, 2010, 2015, 2020, 2025, 2030, 2035];
  const yTicks = [0, 5, 10, 15, 20];
  const padding = { top: 20, right: 15, bottom: 20, left: 25 };

  let width = 500;
  let height = 200;
  $: console.log("client width is: ", width);

  function formatMobile(tick) {
    return "'" + tick.toString().slice(-2);
  }

  $: xScale = scaleLinear()
    .domain([0, xTicks.length])
    .range([padding.left, width - padding.right]);

  $: yScale = scaleLinear()
    .domain([0, Math.max.apply(null, yTicks)])
    .range([height - padding.bottom, padding.top]);

  $: innerWidth = width - (padding.left + padding.right);

  $: barWidth = innerWidth / xTicks.length;
</script>

<h2>US birthrate by year</h2>

<div class="wrapper">
  <h4>Here is the title</h4>
  <h5>Here is the subtitle</h5>
</div>
<div class="chart" bind:clientWidth={width} bind:clientHeight={height}>
  {#if width}
  <svg >
    <!-- Design the bars -->

    <g class="bars">
      {#each points as point, i}
        <rect
          class="bar {i}"
          x={xScale(i) + 2}
          y={yScale(point.birthrate) - yScale(0)}
          width={barWidth - 4}
          height={yScale(0) - yScale(point.birthrate)}
          in:reveal={{ delay: 300 + i * 70, duration: 350 }}
        />
      {/each}
    </g>
    <!-- Design y axis -->
    <g class="axis y-axis">
      <p>Hello this is legend</p>
      {#each yTicks as tick}
        <g class="tick tick-{tick}" transform="translate(0, {yScale(tick)})">
          <line x2="100%" />
          <text y="-4">{tick} {tick === 20 ? " per 1,000 population" : ""}</text
          >
        </g>
      {/each}
    </g>

    <!-- Design x axis -->
    <g class="axis x-axis">
      {#each points as point, i}
        <g class="tick" transform="translate({xScale(i)}, {height})">
          <text x={barWidth / 2} y="-4">
            {width > 380 ? point.year : formatMobile(point.year)}</text
          >
        </g>
      {/each}
    </g>
  </svg>
  {/if}
</div>

<style>
  .chart {
    width: 100%;
    max-width: 500px;
    min-width: 200px;
    margin: 0 auto;
    
   
  }
  .wrapper {
    width: 100%;
    max-width: 500px;
    min-width: 200px;
    margin: 0 auto;

  }
  h2 {
    text-align: center;
    color: #ddd;
  }

  h4 {
    color: #ddd;
    margin: 0 0 5px 0;
  }
  h5 {
    color: #ddd;
    margin: 0 0 5px 0;
  }

  .x-axis .tick text {
    text-anchor: middle;
  }

  svg {
    position: relative;
    width: 100%;
    height: 200px;
    margin-bottom: -20px;


  }


  .bars rect {
    fill: blue;
    stroke: none;
  }
  .tick line {
    stroke: var(--brand_secondary);
    stroke-dasharray: 2;
    opacity: 1;
    z-index: 100000;
  }

  .tick {
    font-family: Helvetica, Arial;
    font-size: 0.725em;
    font-weight: 200;
  }
  .tick text {
    fill: var(--brand_text);
    text-anchor: start;
    padding: 5rem;
  }

  .tick.tick-0 line {
    display: inline-block;
    stroke-dasharray: 0;
  }
</style>
